import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import '../assets/styles/style.css';
import Modal from '@material-ui/core/Modal';
import Backdrop from '@material-ui/core/Backdrop';
import Fade from '@material-ui/core/Fade';
import {HaiList,AnkanList,HuroHaiList,SelectButtonsComponent} from './index.js';
import {haiNumSets,yakuHansuSets} from '../assets/datasets/index.js'

const useStyles = makeStyles((theme) => ({
    modal: {
      display: 'flex',
      alignItems: 'flex-start',
      justifyContent: 'center',
      overflow:'scroll',
      margin:'3vh auto',
    },
    paper: {
      backgroundColor: theme.palette.background.paper,
      border: '2px solid #000',
      boxShadow: theme.shadows[5],
      padding: theme.spacing(2, 4, 3),
    },
}));

const ResultWindow = (props)=>{
    const classes = useStyles();
    const [open, setOpen] = React.useState(false);

    const handleOpen = (setNum)=>{
        setOpen(true);
    }
    const handleClose = ()=>{
        setOpen(false);
    }

    let oyaKo;
    if(props.jiKaze===31){
        oyaKo = "";
    }else{
        oyaKo = "";
    }

    let agarihaiCaution;
    if(props.haiListTehai.some((x)=>x===props.agarihai.hai)){
        agarihaiCaution = null;
    }else{
        agarihaiCaution = ""
    }

    let doraListStr;
    if(props.dorahai.length!==0){
        doraListStr = props.dorahai.map((x)=>{return haiNumSets[x]}).join(',');
    }
    let uradoraListStr;
    if(props.uradorahai.length!==0){
        uradoraListStr = props.uradorahai.map((x)=>{return haiNumSets[x]}).join(',');
    }

    let huStr;
    if(props.result.hu===0){
        huStr = "-";
    }else{
        huStr = String(props.result.hu);
    }

    let pointName="";
    if(props.result.hansu>=39){
        pointName="";
    }else if(props.result.hansu>=26){
        pointName="";
    }else if(props.result.hansu>=13){
        pointName="";
    }else if(props.result.hansu>=11){
        pointName="";
    }else if(props.result.hansu>=8){
        pointName="";
    }else if(props.result.hansu>=6){
        pointName="";
    }else if(props.result.hansu>=5){//4
        pointName="";
    }else if(props.result.hansu>=4 && props.result.hu>=40){//4
        pointName="";
    }else if(props.result.hansu>=3 && props.result.hu>=70){//4
        pointName="";
    }
    //""
    let yakuHansuArr=[];
    const yakumanArr=["","","","(13)","","","","","","","","","()","","",""];
    for(let i=0;i<props.yaku.length;i++){
        if(i<=props.yaku.length-4){
            yakuHansuArr.push(props.yaku[i]+":"+yakuHansuSets[props.yaku[i]]+"");
        }else if(yakumanArr.some((value)=>{return value===props.yaku[i]})){
            yakuHansuArr.push(props.yaku[i]);
        }else{
            yakuHansuArr.push(props.yaku[i]+"");
        }
    }

    return(
        <div>
            <div className="resultWindowButton">
               <SelectButtonsComponent label={"Show Result"} color={"primary"} setNum={1} select={handleOpen} />
            </div>
            <Modal
                aria-labelledby="transition-modal-title"
                aria-describedby="transition-modal-description"
                className={classes.modal}
                open={open}
                onClose={handleClose}
                closeAfterTransition
                BackdropComponent={Backdrop}
                BackdropProps={{
                    timeout: 500,
                }}
            >
                <Fade in={open}>
                    <div className={classes.paper}>
                        <h2 id="transition-modal-title"></h2>
                        <h4></h4>
                        {oyaKo}  {huStr}{props.result.hansu}
                        <h2>{pointName}{props.result.point}</h2>
                        <div className="cautionStr">{agarihaiCaution}</div>
                        {yakuHansuArr.map((value,index)=>{
                            return <div key={index}>{value}</div>
                        })}<br/>
                        {haiNumSets[props.jiKaze]}{haiNumSets[props.baKaze]}{haiNumSets[props.agarihai.hai]}<br/>
                        {doraListStr}{uradoraListStr}<br/><br/>
                        {/**/}
                        <HaiList select={props.selectTehai} haiList={props.haiListTehai} />
                        {/* */}
                        <AnkanList select={props.selectAnkan} haiList={props.haiListAnkan} />
                        {/** */}
                        <HuroHaiList select={props.selectHuro} huro={props.haiListHuro} /> 
                        <br/>
                        <br/>
                        <div className="resultWindowCancelButton">
                            <SelectButtonsComponent label={""} color={"default"} setNum={1} select={handleClose} />
                        </div>
                    </div>
                </Fade>
            </Modal>
        </div>
    )
}

export default ResultWindow;
