import React from 'react';
import {SelectButtonsComponent} from './index.js';

const RichiList = (props)=>{
    //
    let colorFlag = ["primary","primary","primary"];
    if(props.richi!==0){
        colorFlag[props.richi-1]="secondary";    
    }
    return(
        
        <div className="selectButtons">
            <SelectButtonsComponent label={""} color={colorFlag[0]} setNum={1} select={props.select} />
            <SelectButtonsComponent label={""} color={colorFlag[1]} setNum={2} select={props.select} />
            <SelectButtonsComponent label={""} color={colorFlag[2]} setNum={3} select={props.select} />
        </div>
    )
}

export default RichiList;