import React from 'react';
import {Kaze} from './index.js';

const KazeList = (props) => {
    //
    let jiKazeColorFlag = ["primary","primary","primary","primary"];
    jiKazeColorFlag[props.jiKaze%30-1]="secondary";
    //
    let baKazeColorFlag = ["primary","primary","primary","primary"];
    baKazeColorFlag[props.baKaze%30-1]="secondary";

    return(
        <div>
            <br/>
            <div className="selectButtons">
                <Kaze kaze={""} jiBa={0} color={jiKazeColorFlag[0]} select={props.select}/>
                <Kaze kaze={""} jiBa={0} color={jiKazeColorFlag[1]} select={props.select}/>
                <Kaze kaze={""} jiBa={0} color={jiKazeColorFlag[2]} select={props.select}/>
                <Kaze kaze={""} jiBa={0} color={jiKazeColorFlag[3]} select={props.select}/>
            </div>
            <br/>
            <div className="selectButtons">
                <Kaze kaze={""} jiBa={1} color={baKazeColorFlag[0]} select={props.select}/>
                <Kaze kaze={""} jiBa={1} color={baKazeColorFlag[1]} select={props.select}/>
                <Kaze kaze={""} jiBa={1} color={baKazeColorFlag[2]} select={props.select}/>
                <Kaze kaze={""} jiBa={1} color={baKazeColorFlag[3]} select={props.select}/>
            </div>
        </div>
    )
}

export default KazeList;