import React from 'react';
import {SelectButtonsComponent} from './index.js';

const HaiteiList = (props)=>{
    //
    let colorFlag = ["primary","primary"];
    if(props.haitei!==0){
        colorFlag[props.haitei-1]="secondary";    
    }
    return(
        <div className="selectButtons">
            <SelectButtonsComponent label={""} color={colorFlag[0]} setNum={1} select={props.select} />
            <SelectButtonsComponent label={""} color={colorFlag[1]} setNum={2} select={props.select} />
        </div>
    )
}

export default HaiteiList;